/* @flow */

import React from 'react';
import styled from 'styled-components';
import sample from 'lodash/sample';
import ReactAudioPlayer from 'react-audio-player';
import song1 from '../../socialMedia/Music/01_Morpheo.mp3';
import song2 from '../../socialMedia/Music/02_Asher_Quinn_Greensleeves.mp3';
import song3 from '../../socialMedia/Music/03.mp3';
import song4 from '../../socialMedia/Music/04.mp3';
import song5 from '../../socialMedia/Music/05.mp3';
import song6 from '../../socialMedia/Music/06.mp3';
import song7 from '../../socialMedia/Music/07.mp3';
import song8 from '../../socialMedia/Music/08.mp3';
import song9 from '../../socialMedia/Music/09.mp3';
import Link from '../Link';
import {ContentSecCTA} from '../common';

const Container = styled.div`
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%;
  background: #362c2a;
  padding: 10px 10px 0 10px;
  box-sizing: border-box;
`;

const PlayerButton = ContentSecCTA.extend`
  margin-right: 5px;
  padding: 0 11px;
  background: #e72f34;
  transition: 0.3s all;
`;

const PlayerButtonActive = PlayerButton.extend`
  box-shadow: 0px 2px #9e1e21;
  transform: translate(0px, 2px);
  background: #ffeac9;
  color: #362c2a;
  transition: 5s all;
`;

const StyledReactAudioPlayer = styled(ReactAudioPlayer)`
  width: 100%;
  display: block;
  margin-bottom: 10px;
`;


type Props = {};

const songs = [
  song1,
  song2,
  song3,
  song4,
  song5,
  song6,
  song7,
  song8,
  song9,
];

class Component extends React.Component<Props> {
  state = {
    songIndex: songs.indexOf(sample(songs)),
    songs,
    playing: false,
  };

  render() {
    const currentSongIndex = this.state.songIndex;
    return (
      <Container>
        <StyledReactAudioPlayer
          src={this.state.songs[this.state.songIndex]}
          preload="metadata"
          controls
          autoPlay={this.state.playing}
          onEnded={this.onSongEnded}
          onPlay={this.onSongPlay}
          onPause={this.onSongPause}
        />
        <div>
          <PlayerButton onClick={this.onPrevious} href="" trackingName="audioPlayer"
                        trackingValue="previous">Previous</PlayerButton>
          <PlayerButton onClick={this.onNext} href="" trackingName="audioPlayer"
                        trackingValue="next">Next</PlayerButton>
          {this.state.songs.map((song, songIndex) => {
            const commonProps = {
              href: ``,
              key: song,
              trackingValue: `songN${songIndex + 1}`,
              trackingName: "audioPlayer",
            };
            if (currentSongIndex === songIndex) {
              return <PlayerButtonActive {...commonProps}>{songIndex + 1}</PlayerButtonActive>
            }
            return <PlayerButton {...commonProps}
                                 onClick={() => this.goToSong(songIndex)}>{songIndex + 1}</PlayerButton>
          })}
        </div>
      </Container>
    );
  }

  goToSong = (songIndex) => {
    this.setState({
      songIndex,
      playing: true,
    });
  };

  onPrevious = () => {
    const songIndex = this.state.songIndex === 0 ? this.state.songs.length - 1 : this.state.songIndex - 1;
    this.setState({
      songIndex,
      playing: true,
    });
  };

  onNext = () => {
    const songIndex = this.state.songIndex + 1 >= this.state.songs.length ? 0 : this.state.songIndex + 1;
    this.setState({
      songIndex,
      playing: true,
    });
  };

  onSongPlay = () => {
    this.setState({
      playing: true,
    });
    console.log(this.state)
  };

  onSongPause = () => {
    this.setState({
      playing: false,
    });
    console.log(this.state)
  };

  onSongEnded = () => {
    if (this.state.songIndex + 1 >= this.state.songs.length) {
      this.setState({
        songIndex: 0,
        playing: true,
      });
      console.log(this.state)
    } else {
      this.setState({
        songIndex: this.state.songIndex + 1,
        playing: true,
      });
      console.log(this.state)
    }
  }
}

export default Component;



